@mixin ellipsis() {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@mixin ballonIn() {
  position: relative;
  padding: 1rem;
  z-index: zindex(dropdown) + 3;
  background-color: #fff;
}

@mixin arrow-line($size: 1rem, $color: #000) {
  &:before {
    display: block;
    content: "";
    width: $size;
    height: $size;
    border-width: 2px;
    border-style: solid solid none none;
    border-color: $color;
    transform: rotate(45deg);
    position: absolute;
  }
}

@mixin arrow-fill($color: #000, $size: 0.4rem) {
  &:before {
    display: block;
    content: "";
    width: 0;
    height: 0;
    border-width: $size;
    border-style: solid;
    border-color: $color $color transparent transparent;
    transform: scaleX(0.7) rotate(135deg);
    position: absolute;
  }
}

@mixin arrow-fill-up() {
  &:before {
    transform: translateY(7px) scaleX(0.7) rotate(315deg);
  }
}

@mixin close($color: #000, $size: 1rem) {
  &:before,
  &:after {
    display: block;
    content: "";
    width: $size;
    height: 0.2rem;
    background-color: $color;
    @include center("y");
  }
  &:before {
    transform: rotate(45deg);
  }
  &:after {
    transform: rotate(-45deg);
  }
}

@mixin invalid() {
  // border-color: $color-invalid;
}

@mixin arrow-right() {
  &:after {
    display: block;
    content: "";
    position: absolute;
    width: 98%;
    height: 0.2rem;
    background-color: #000;
    top: 50%;
    transform: translateY(-50%);
  }
  @include arrow-fill();
  &:before {
    transform: translateY(-50%) scaleY(0.6) scaleX(1) rotate(45deg);
    right: 0;
    top: 50%;
  }
}
